<template>
    <div>
        <pdf-panel>
            <div class="qiong-tr">
                <div class="qiong-wide-10">Member ID</div>
                <div class="qiong-wide-40">Member Name</div>
                <div class="qiong-wide-13">Payment Level</div>
                <div class="qiong-wide-13">Order No.</div>
                <div class="qiong-wide-12">Order Pv</div>
                <div class="qiong-wide-12 pr-0">Bonus Pv</div>
            </div>
            <qiong-space :space="'0.2vw'"></qiong-space>
            <div>
                <div class="qiong-td" v-for="(v, i) in buying" :key="i">
                    <div class="qiong-wide-10">{{ v.users_permissions_user.number_code }}</div>
                    <div class="qiong-wide-40">{{ v.users_permissions_user.display_name }}</div>
                    <div class="qiong-wide-13">{{ v.user_sponser.index - _mine.index }}</div>
                    <div class="qiong-wide-13 pr-0">{{ v.order_code }}</div>
                    <div class="qiong-wide-12">{{ v.product.pv * v.num }} </div>
                    <div class="qiong-wide-12">{{ v.user_sponser.bonus.usd_active_house }}</div>
                </div>
            </div>
            
        </pdf-panel>

        <pdf-result :res="result"></pdf-result>
    </div>
</template>

<script>
import PdfPanel from '../../../../components/Pdf/PdfPanel.vue'
import PdfResult from '../../../../components/Pdf/PdfResult.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'

    export default {
  components: { PdfPanel, QiongSpace, PdfResult },
        name: '',
        props: {
            _childs_s: {
                type: Array
            },

            _user: {
                type: Object
            },
            _mine: {
                type: Object
            },
            _bonus: {
                type: Object
            },

            _chronu_id: {
                type: String
            }
        },
        created() {
            this.ratios = this.bonus.conf.RECOMMEND_RATIOS
        },
        data() {
            return {
                buying: null,
                ratios: [ ],

                total: 0
            }
        },
        mounted() {
            this.buying = this.buyingLoading()
        },
        computed: {
            result() {
                this.total = this._mine.bonus.usd_active_house / this.bonus.conf.ACTIVE_USD
                return [
                    {
                        txt: 'Total Active Branch Credit',
                        content: `${ this.total }`
                    },
                    {
                        txt: 'Total Bonus',
                        content: `US$ ${this._mine.bonus.usd_active_house }`
                    }
                ]
            }
        },
        methods: {
            buyingLoading() {
                let listing = [ ]
                const resource = this._childs_s.filter(e => {
                    if (e.index - this._mine.index <= 2) { return true }
                })

                resource.map(async e => {
                    
                    let res = await this.conn.get(
                        this.api.buys,
                        this.$store.state.token,
                        {
                            'chronu': this._chronu_id,
                            'users_permissions_user': e.id,
                            '_sort': 'published_at:DESC'
                        }
                    )

                    if (res) {
                        res.map(j => {
                            j.user_sponser = e
                            listing.push(j)

                        })
                    }
                    
                })

                return listing
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


