<template>
    <div class="pb-3 qiong-pt-10  text-right">
        <qiong-page-ling v-if="bigger == 1"></qiong-page-ling>
        <qiong-page-ling v-else :ex_class="'qiong-lineXL'"></qiong-page-ling>

        <qiong-space :space="'0.2vw'"></qiong-space>
        <qiong-space :space="'0.5vh'"></qiong-space>

        
        <div class="">
            <slot></slot>
        </div>

        <div v-if="bigger == 1">

            <div class="qiong-tr qiong-txt-tr"
                v-for="(v, i) in res" :key="i"
            >
                <div class="qiong-wide-50">&nbsp;</div>

                <div class="qiong-wide-32">
                    {{ v.txt }}<span class="qiong-txt-18">&nbsp;</span>
                </div>
                <div class="qiong-wide-18 pr-0 qiong-txt-18 "
                    v-html="v.content"></div>
            </div>
        </div>
        <div v-else class="">

            <div class="qiong-tr qiong-txt-tr my-0 py-0"
                v-for="(v, i) in res" :key="i"
            >
                <div class="qiong-wide-60">&nbsp;</div>

                <div class="qiong-wide-30">
                    {{ v.txt }}<span class="qiong-txt-18">&nbsp;</span>
                </div>
                <div class="qiong-wide-10 pr-0 qiong-txt-18 "
                    v-html="v.content"></div>
            </div>
        </div>

        <qiong-space :space="'0.2vw'"></qiong-space>
        <qiong-space :space="'0.7vh'"></qiong-space>
    </div>
</template>

<script>
import QiongPageLing from '../QiongPageLing.vue'
import QiongSpace from '../Ui/QiongSpace.vue'
    export default {
        props: {
            res: {
                type: Array,
            },
            bigger: {
                type: Number,
                default: 1
            }
        },
        name: '',
        components: {

            QiongPageLing,
                QiongSpace          
        },
        data() {
            return {
                test: [
                    {
                        txt: '獎金',
                        content: 'HK&nbsp;135.0'
                    },
                    {
                        txt: '獎金',
                        content: 'HK&nbsp;135.0'
                    },
                ]
            }
        },
        mounted() {
            
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


