<template>
    <div v-if="unid">
        <pdf-user-inner :_unid="unid" :_chronu_id="chronu_id" v-if="$store.state.users_sponser"></pdf-user-inner>
        <div v-else>
            <qiong-space :space="'12vh'"></qiong-space>
            <qiong-loading></qiong-loading>
        </div>
    </div>

    <div v-else>

    </div>
</template>

<script>
import QiongLoading from '../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../components/Qiong/Ui/QiongSpace.vue'
import PdfUserInner from './PdfUserInner.vue'
    export default {
        components: { PdfUserInner, QiongSpace, QiongLoading },
        
        name: '', 
        data() {
            return {
                unid: null,
                chronu_id: null
            }
        },
        created() {
            const id = this.$route.query.unid 
            const chronu = this.$route.query.chronu_id 
            this.valuation(id, chronu)
        },
        methods: {
            
            valuation(id = null, chornu = null) {
                if (id) {
                    this.unid = id
                } else {
                    this.unid = this.$store.state.user_backend.unid
                }

                if (chornu) {
                    this.chronu_id = chornu
                } else {
                    this.chronu_id = this.$store.state.chronus.id
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


