<template>
    <pdf-panel>
        <div v-if="_bonus">
            <div class="qiong-td">
                <div class="qiong-wide-30">
                    User Name:&nbsp;{{ _user.username }}
                </div>
                <div class="qiong-wide-30">
                    Member ID:&nbsp;{{ _user.number_code }}
                </div>
                <div class="qiong-wide-30"></div>
                <div class="qiong-wide-10"></div>
            </div>
            <div class="qiong-td">
                <div class="qiong-wide-30">
                    Payment Title:&nbsp;<level-viewing :item="_mine.vip.named + '_' + _mine.vip.star" class="d-inline"></level-viewing>
                </div>
                <div class="qiong-wide-70">
                    Member Name:&nbsp;{{ _user.display_name }}
                </div>
            </div>
            <div class="qiong-td">
                <div class="qiong-wide-30">
                    Print Date:&nbsp;{{ view.getTodayEn() }}
                </div>
                <div class="qiong-wide-70">
                    Bonus Period:&nbsp;{{ view.ser_timed(_bonus.chronu.start, true) }}至&nbsp;{{ view.ser_timed(_bonus.chronu.end, true) }}
                </div>
            </div>
        </div>
    </pdf-panel>
</template>

<script>
import PdfPanel from '../../../../components/Pdf/PdfPanel.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
  components: { PdfPanel, LevelViewing },
        name: '',
        props: {
            _user: {
                type: Object 
            },
            _mine: {
                type: Object
            },
            _bonus: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


