<template>
    <pdf-template>
        <div slot="starter">
            <buy-me ref="refBuyMe"></buy-me>
            <user-me ref="refUserMe"></user-me>
            <bonus-me ref="refBonusMe"></bonus-me>
            <chronu-me red="refChronuMe"></chronu-me>
        </div>

        <div slot="header">
            <pdf-user-header></pdf-user-header>
        </div>

        <div slot="content_1">
            <pdf-header>Member Detail</pdf-header>
            <pdf-user-msg 
                v-if="bonusing"
                :_mine="mine" :_user="user" :_bonus="bonusing"></pdf-user-msg>
            <qiong-space :space="'1vw'"></qiong-space>

            <pdf-header>Individual Pv</pdf-header>
            <pdf-user-buy 
                v-if="bonusing"
                :_buy="buying"  :_mine="mine" :_user="user" :_bonus="bonusing"></pdf-user-buy>
            <qiong-space :space="'1vw'"></qiong-space>

            <pdf-user-credit :_mine="mine" v-if="mine"></pdf-user-credit>
            <qiong-space :space="'2.4vw'"></qiong-space>

        </div>

        <div slot="content_2">
            <div class="pdf-px">Bonus Detail</div>
            <qiong-space :space="'1.4vw'"></qiong-space>
            <pdf-header 
                v-if="mine">Enroller Referral bonus for first purchase</pdf-header>
            <pdf-user-enroller-ref 
                v-if="childrens_enroller"
                :_chronu_id="chronu_id"
                :_user="user" :_mine="mine" :_bonus="bonusing" :_childs_e="childrens_enroller"></pdf-user-enroller-ref>
            <qiong-space :space="'1.8vw'"></qiong-space>

            <pdf-header>Active Branch Bonus</pdf-header>
            <pdf-user-active-house 
                v-if="childrens_sponser"
                :_chronu_id="chronu_id"
                :_user="user" :_mine="mine" :_bonus="bonusing" :_childs_s="childrens_sponser"></pdf-user-active-house>
            <qiong-space :space="'1.8vw'"></qiong-space>

            <pdf-header>Organization Bonus</pdf-header>
            <pdf-user-team-layer
                v-if="childrens_sponser"
                :_chronu_id="chronu_id"
                :_user="user" :_mine="mine" :_bonus="bonusing" :_childs_s="childrens_sponser"></pdf-user-team-layer>
        </div>

        <pdf-user-footer slot="footer" :me="mine" v-if="mine"></pdf-user-footer>
    </pdf-template>
        
</template>

<script>
import BonusMe from '../../../components/Data/Using/BonusMe.vue'
import BuyMe from '../../../components/Data/Using/BuyMe.vue'
import ChronuMe from '../../../components/Data/Using/ChronuMe.vue'
import UserMe from '../../../components/Data/Using/UserMe.vue'
import PdfHeader from '../../../components/Pdf/PdfHeader.vue'


import PdfTemplate from '../../../components/Pdf/PdfTemplate.vue'
import QiongSpace from '../../../components/Qiong/Ui/QiongSpace.vue'

import PdfUserActiveHouse from './Bonus/PdfUserActiveHouse.vue'
import PdfUserEnrollerRef from './Bonus/PdfUserEnrollerRef.vue'

import PdfUserTeamLayer from './Bonus/PdfUserTeamLayer.vue'
import PdfUserBuy from './Inner/PdfUserBuy.vue'
import PdfUserCredit from './Inner/PdfUserCredit.vue'
import PdfUserFooter from './Inner/PdfUserFooter.vue'
import PdfUserHeader from './Inner/PdfUserHeader.vue'
import PdfUserMsg from './Inner/PdfUserMsg.vue'

    export default {
        props: {
            _unid: {
                type: String,
                default: ''
            },
            _chronu_id: {
                type: String,
                default: ''
            }
        },
        components: { 
            PdfTemplate, PdfUserFooter, 
            BuyMe, BonusMe, UserMe, PdfUserMsg, 
            ChronuMe, PdfHeader, QiongSpace,
                PdfUserBuy,
                PdfUserCredit,
                PdfUserEnrollerRef,
                PdfUserActiveHouse ,
                PdfUserTeamLayer,
                PdfUserHeader  
        },
        name: '',
        data() {
            return {
                unid: null,
                chronu_id: null,

                bonusing: null,
                buying: null,
                user: null,

                mine: null,
                chornu: null,
                childrens_sponser: null,
                childrens_enroller: null,
            }
        },
        created() {
            this.valuation(this._unid, this._chronu_id)
        },
        mounted() {
            console.log('打印, unid =', this.unid)
            this.dataLoading()
        },
        methods: {
            // 初始化赋值
            valuation(id = null, chornu = null) {
                if (id) {
                    this. unid = id
                } else {
                    this.unid = this.$store.state.user_backend.unid
                }

                if (chornu) {
                    this.chronu_id = chornu
                } else {
                    this.chronu_id = this.$store.state.chronus.id
                }
            },
            // 获取资料
            async dataLoading() {
                const b = await this.$refs.refBonusMe.bonusLoading(this.unid, this.chronu_id)
                this.buying = await this.$refs.refBuyMe.buyLoading(this.unid, this.chronu_id)
                const u = await this.$refs.refUserMe.userLoading(this.unid)

                this.user = u[0]
                this.bonusing = b[0] 

                console.log(this.unid)
                this.lockingMine()
                this.switchChildren('SPONSER')
                this.switchChildren('ENROLLER')

                if (this.$refs.refBonusMe.bonused) {
                    if (this.$refs.refBuyMe.buyed) {

                    }
                }

                console.log('fecthing DATA =', this.buying, 'bonus =', this.bonusing, this.childrens_enroller)
            },


            // 锁定人群
            async lockingMine() {
                const _this = this
                let many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                
                if (many) {
                    this.bonus._build_res(many, transfer, function(u, t) {
                        if (u.unid == _this.unid) {
                            _this.mine = JSON.parse(JSON.stringify(u))
                        }
                    })

                    await this.bonus.team_layer( [ this.mine ], transfer)
                    await this.bonus.recommend( [ this.mine ], transfer )
                }
            },

            // 获取子成员
            switchChildren(mode = 'SPONSER') {
                const _this = this
                let many = [ ]
                const transfer = this.$store.state.transfer

                if (mode == 'SPONSER') {
                    many = this.$store.state.users_sponser
                    this.childrens_sponser = [ ]
                    this.bonus._build_res([ this.mine ], transfer, function(u, t) {
                        if (u.unid != _this.mine.unid) { _this.childrens_sponser.push(u) }
                    })
                } else {
                    many = this.$store.state.users_enroller
                    this.childrens_enroller = [ ]
                    this.bonus._build_res([ this.mine ], transfer, function(u, t) {
                        if (u.unid != _this.mine.unid) { _this.childrens_enroller.push(u) }
                    })
                }
            }
            
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


