<template>
    <div class="fs-L">

        <div class="pdf-px">
            <div class="fs-XL fw-b">NOTES</div>
            <div>
                1. Current month excahnge rate&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                US$&nbsp;&nbsp;&nbsp;&nbsp;
                                <span class="fw-b">
                                    {{
                                        resault
                                    }}
                                </span>
            </div>
            <div>
                2. Please examine this statement at once. If no error is reported within 14 days, it will be considered as correct.
            </div>
            <div>
                3. Please print out this statement for your own retention.
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props: {
            me: {
                type: Object
            }
        },
        computed: {
            resault() {
                let res = 0

                if (this.me) {
                    res = this.view.floatAdd(
                        this.me.bonus.usd_recommend,
                        this.me.bonus.usd_team_layer
                    )
                    res = this.view.floatAdd(
                        res,
                        this.me.bonus.usd_active_house
                    )
                }

                return res
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


