<template>
    <div class="d-inline-block">
        <div class="qiong-named_space" v-for="i in index" :key="i"></div>
        <div class="qiong-named_icon">
            <v-icon
                v-if="need_icon"
                class="qiong-icon-primary-thin qiong-txt-shadow-T">mdi-minus-circle-outline</v-icon>
        </div>
        {{ named }}
    </div>
</template>

<script>
    export default {
        props: {
            named: {
                type: String,
            },
            index: {
                type: Number
            },
            need_icon: {
                type: Boolean,
                default: true
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.qiong-named_space
    width: calc(8px + 1vw) !important
    height: 10px
    min-height: 5px
    display: inline-block

.qiong-named_icon
    width: calc(4px + 0.5vw) !important
    height: calc(4px + 0.5vw) !important
    min-height: 8px
    display: inline-block
.qiong-named_icon i
    font-size: calc(4px + 0.5vw) !important
</style>


