<template>
    <pdf-panel>
        <div>
            <div class="qiong-tr">
                <div class="qiong-wide-24">Order Date:</div>
                <div class="qiong-wide-15">Member ID</div>
                <div class="qiong-wide-12">Role</div>
                <div class="qiong-wide-15">Order No.</div>
                <div class="qiong-wide-15">Order Points</div>
                <div class="qiong-wide-19"></div>
            </div>
            <qiong-space :space="'0.2vw'"></qiong-space>
            <div>
                <div class="qiong-td"
                    v-for="(v, i) in _buy" :key="i"
                >
                    <div class="qiong-wide-24">{{ view.ser_timed(v.buy_time, true) }}</div>
                    <div class="qiong-wide-15">{{ v.origin_buyer_number_code }}</div>
                    <div class="qiong-wide-12">{{ bonus.has.order_role(v.role) }}</div>
                    <div class="qiong-wide-15">{{ v.order_code }}</div>
                    <div class="qiong-wide-15 pr-0">{{ v.num * v.product.pv }}PV</div>
                    <div class="qiong-wide-19"></div>
                </div>
            </div>
        </div>
    </pdf-panel>
</template>

<script>
import PdfPanel from '../../../../components/Pdf/PdfPanel.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'
    export default {
  components: { PdfPanel, QiongSpace },
        name: '',
        props: {
            _buy: {
                type: Array  
            },
            _user: {
                type: Object 
            },
            _mine: {
                type: Object
            },
            _bonus: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


