<template>
    <div>
        <pdf-header>Bonus Eligibility:&nbsp;
            <span v-if="_mine.ipv >= bonus.conf.USER_PV_LIMIT">Active</span>
            <span v-else>Non Active</span>

        </pdf-header>
        <div class="pdf-px">
            <div class="qiong-td">
                <div class="qiong-wide-24">
                    Individual Credit:&nbsp;{{ _mine.ipv }}
                </div>
                <div class="qiong-wide-26">
                    Organization Credit:&nbsp;{{ _mine.son_pv }}
                </div>
                <div class="qiong-wide-40">
                    Direct Enroll Member (active):&nbsp;{{ _mine.son_can_num }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PdfHeader from '../../../../components/Pdf/PdfHeader.vue'
import PdfPanel from '../../../../components/Pdf/PdfPanel.vue'
    export default {
        name: '',
        components: {
            PdfHeader,
                PdfPanel
        },
        props: {
            _mine: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


