<template>
    <div>
        <pdf-panel>
                <div class="qiong-tr">
                <div class="qiong-wide-10">Member ID</div>
                <div class="qiong-wide-30">Member Name</div>
                <div class="qiong-wide-13 pr-0">Payment Level</div>
                <div class="qiong-wide-10">Order No.</div>
                <div class="qiong-wide-9">Order Pv</div>
                <div class="qiong-wide-16 pr-0">Bonus Percentage</div>
                <div class="qiong-wide-12">Bonus Pv</div>
            </div>
            <qiong-space :space="'0.2vw'"></qiong-space>
            <div>
                <div class="qiong-td" v-for="(v, i) in buying" :key="i">
                    <div class="qiong-wide-10">{{ v.users_permissions_user.number_code }}</div>
                    <div class="qiong-wide-30">{{ v.users_permissions_user.display_name }}</div>
                    <div class="qiong-wide-13">{{ v.user_sponser.temporary_index }}</div>
                    <div class="qiong-wide-10 pr-0">{{ v.order_code }}</div>
                    <div class="qiong-wide-9">{{ v.product.pv * v.num }} </div>
                    <div class="qiong-wide-16"> {{ view.floatMul(_mine.vip.ratio[v.user_sponser.temporary_index - 1],  100) }}%</div>
                    <div class="qiong-wide-11">{{ view.floatMul(_mine.vip.ratio[v.user_sponser.temporary_index - 1],  v.user_sponser.ipv) }}</div>
                    <div class="qiong-wide-1" v-if="((i + 1) === ken)">
                    </div>
                </div>
            </div>
            
        </pdf-panel>

        <pdf-result :res="result"></pdf-result>
        
        <pdf-printer :is_print="true" v-if="print"></pdf-printer>
    </div>
</template>

<script>
import PdfPanel from '../../../../components/Pdf/PdfPanel.vue'
import PdfPrinter from '../../../../components/Pdf/PdfPrinter.vue'
import PdfResult from '../../../../components/Pdf/PdfResult.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'
    export default {
  components: { PdfPanel, QiongSpace, PdfPrinter, PdfResult },
        name: '',
        props: {
            _childs_s: {
                type: Array
            },

            _user: {
                type: Object
            },
            _mine: {
                type: Object
            },
            _bonus: {
                type: Object
            },

            _chronu_id: {
                type: String
            }
        },
        created() {
            this.ratios = this.bonus.conf.RECOMMEND_RATIOS
        },
        data() {
            return {
                buying: null,
                ratios: [ ],
                ken: -1,

                print: false,

                total: 0
            }
        },
        mounted() {
            this.print = false
            this.buying = this.buyingLoading()
        },
        computed: {
            result() {
                return [
                    {
                        txt: 'Total Pv',
                        content: `${ this.total }`
                    },
                    {
                        txt: 'Total Bonus',
                        content: `US$ ${this._mine.bonus.usd_team_layer }`
                    }
                ]
            }
        },
        methods: {
            buyingLoading() {
                let listing = [ ]
                const resource = this._childs_s.filter(e => {
                    if (e.index - this._mine.index <= 2) { return true }
                })

                resource.map(async e => {
                    
                    let res = await this.conn.get(
                        this.api.buys,
                        this.$store.state.token,
                        {
                            'chronu': this._chronu_id,
                            'users_permissions_user': e.id,
                            '_sort': 'published_at:DESC'
                        }
                    )

                    if (res) {
                        res.map(j => {
                            j.user_sponser = e
                            listing.push(j)

                            this.total = this.view.floatAdd(this.total, ( j.product.pv * j.num ))
                        })
                    }

                    this.ken += 1
                    if (this.ken == (resource.length - 1)) { this.print = true }
                })

                return listing
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


