<template>
    <div class="d-inline">
        <div v-if="bigger == -1" class="vip-img-S" :class="extra_class">
            <img :src="result" v-if="result">
            <div v-else class="simpler-avatar sa-s">
                Lv.0
            </div>
        </div>
        <div v-if="bigger == 2" class="vip-img-XXL" :class="extra_class">
            <img :src="result" v-if="result">
            <div v-else class="simpler-avatar sa-XXL">
                Lv.0
            </div>
        </div>
    </div>
</template>

<script>
import api_bonus from '@/common/es/api_bonus/index'

    export default {
        props: {
            item: {
                type: String
            },
            def: {
                type: String,
                default: 'ICONS/vip_simpler_1.png'
            },
            bigger: {
                type: Number,
                default: -1
            },

            extra_class: {
                type: String
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
            
        },
        computed: {
            result() {
                let addr = '/static/'

                if (this.item != undefined && this.item != 'undefined_0') { 
                    let res = api_bonus.vip_view.split_code(this.item)
                    res = api_bonus.vip_view.code_to_content( res )

                    if (res) { 
                        addr += res.img 
                    } else { 
                        addr += this.def 
                    }
                    return addr
                }

                return null
            },
        },
    }
</script>

<style lang="sass" scoped>
.vip-img-S,
.vip-img-XXL,
.vip-img-S img,
.vip-img-XXL img
    display: inline
    width: 15px !important
    height: auto

.vip-img-XXL ,
.vip-img-XXL img
    width: calc( 30px + 3.7vw ) !important

.simpler-avatar
    background-color: white
    display: flex
    align-items: center
    justify-content: center
    height: 100%
    border-radius: 100%

    font-size: 18px
    font-weight: bold
    color: #9e9e9e

.sa-XXL
    margin-right: 6px
    min-width: 6vw
    min-height: 6.3vw
</style>


